<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'
    import PartnerAboutTextTab from '@/components/partner/PartnerAboutTextTab.vue';

    export default {
        metaInfo() {
            return {
                title: 'Рейтинг скидок',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "SuppliersRating",
        components: {
          Popup,
          PartnerAboutTextTab,
        },
        data: function () {
            return {
              suppliers: [{name: 'СтройПремиум', count: '12', sum: '454 738', id: 1},
              {name: 'E-монтаж', count: '8', sum: '217 019', id: 2},
              {name: 'LINELLI', count: '11', sum: '28 391', id: 3},
            {name: 'СтройПремиум', count: '11', sum: '28 391', id: 4}]
            }
        },
        created() {

        },
        mounted() {
            // ym(39511350, 'hit', this.$route.path, {title: 'Рейтинг скидок | ZemSkidki.ru'});
        },
        computed: {
            ...mapState({
                currentCity:  state => state.cities.current,
                coupons:  state => state.rating.coupons,
                sort:  state => state.rating.sort.current,
                popups: state => state.popups.items,
                action: state => state.actions.detail,
            }),
        },
        methods: {
            activeItem: function () {
                  this.$store.dispatch('actions/goCoupon', {id:3554});
            },
            setSort: function (sortField) {
                this.$store.dispatch('rating/setSort', {order: sortField});
                this.$store.dispatch('rating/getData', {action: 'sort'});
            },
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.body.clientHeight;
                if (bodyHeight - 80 <= scroll + viewport) {
                    this.$store.dispatch('rating/getData', {action: 'page'});
                }
            },
            supplierPopupOpen: function(){
              this.$store.dispatch('popups/open', {name: this.popups.supplierRating.name});
            },
            supplierPopupClose: function(){
              this.$store.commit('popups/close', {name: this.popups.supplierRating.name});
            },
        },
        directives: {
            'clickoutside': {
                bind: function(el, binding, vNode) {
                    if (typeof binding.value !== 'function') {
                        const compName = vNode.context.name;
                        let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                        if (compName) { warn += `Found in component '${compName}'` }
                        //console.warn(warn);
                    }
                    const bubble = binding.modifiers.bubble;
                    const handler = (e) => {
                        if (bubble || (!el.contains(e.target) && el !== e.target) && (!e.target.classList.contains('click-outside-tooltip'))) {
                            binding.value(e)
                        }
                    }
                    el.__vueClickOutside__ = handler;
                    document.addEventListener('click', handler);
                },

                unbind: function(el, binding) {
                    document.removeEventListener('click', el.__vueClickOutside__);
                    el.__vueClickOutside__ = null;
                }
            }
        }
    }
</script>
